var that;
import orderlist from '../../order/order.js';
export default {
	data() {
		return {
			gid: '',
			goodslist: [],
			data: {},
			paylist: [
				{
					img: '/static/img/icon/icon-wxpay.png',
					name: '微信支付',
					id: '0',
					type: 'WECHAT_PAY'
				},








				{
					img: '/static/img/icon/iocn-lqpay.png',
					name: '零钱支付',
					id: '0',
					type: 'BALANCE_PAY'
				}
			],
			cardpay: [
				{
					img: '/static/img/icon/icon-20.png',
					name: '定制卡支付',
					id: '0',
					type: 'CUSTOM_PAY'
				}
			],
			dzpay: false,
			payaction: -1,
			content: '',
			dzcard: [],
			modal: false
		};
	},
	methods: {
		tpwl(e) {
			let detai_id = e.currentTarget.dataset.order_detai_id ? e.currentTarget.dataset.order_detai_id : '';
			uni.navigateTo({
				url: '/pages/order/shopOrderinfo/shopOrderinfo?id=' + that.data.order_id + '&detail_id=' + detai_id
			});
		},
		getinfo() {
			this.$request
				.get(
					{
						url: 'api/order/detail',
						data: {
							order_id: that.gid
						}
					},
					'正在加载'
				)
				.then(res => {
					if (res.code == 1) {
						this.$alert(res.msg);
					} else {
						console.log('详情：', res);
						that.goodslist = res.data.goods_list;
						that.data = res.data;
					}
				});
		},
		orderRevoke: function(e) {
			var a = this;
			that.tanchuang
				.showModel({
					title: '提示',
					content: '是否取消该订单',
					cancelText: '否',
					confirmText: '是'
				})
				.then(res => {
					if (res == 'confirm') {
						that.$request
							.get(
								{
									url: 'api/order/revoke',
									data: {
										order_id: e.currentTarget.dataset.id
									}
								},
								'正在执行'
							)
							.then(res => {
								if (res.code == 1) {
									that.$alert(res.msg);
								} else {
									console.log(res);
									that.$alert(res.msg);
									that.list = [];
									setTimeout(function() {
										that.getinfo();
									}, 1000);
								}
							});
					}
				});

			// uni.showModal({
			// 	title: "提示",
			// 	content: "是否取消该订单",
			// 	cancelText: "否",
			// 	confirmText: "是",
			// 	success: function(t) {
			// 		if (t.cancel) return !0;
			// 		t.confirm && (
			// 			that.$request
			// 			.get({
			// 				url: 'api/order/revoke',
			// 				data: {
			// 					order_id: e.currentTarget.dataset.id
			// 				}
			// 			}, '正在执行')
			// 			.then(res => {
			// 				if (res.code == 1) {
			// 					that.$alert(res.msg);
			// 				} else {
			// 					console.log(res);
			// 					that.$alert(res.msg);
			// 					that.list=[];
			// 					setTimeout(function() {
			// 						that.getinfo();
			// 					}, 1000);
			// 				}
			// 			})
			// 		);
			// 	}
			// });
		},
		// 选择普通支付方式
		getpaytype(e) {
			if (that.payaction == e.currentTarget.dataset.index) {
				that.payaction = -1;
			} else {
				that.payaction = e.currentTarget.dataset.index;
			}
			that.$forceUpdate();
		},
		// 选择定制卡支付
		clickdz() {
			that.dzpay = !that.dzpay;
			if (that.dzpay) {
				that.hideModal();
			}
		},
		// 控制弹窗
		hideModal(e) {
			that.modal = !that.modal;
		},
		// 定制卡点击确认
		dzcomp() {
			that.hideModal();
			var dzmoney = 0;
			var zmoney = that.new_total_price;
			// 选择定制卡支付
			if (that.payaction == 0) {
				if (that.customs.length == 0) {
					that.$alert('请选择要支付的定制卡!');
					return;
				} else {
					// 判断定制卡金额是否够
					for (let t = 0; t < that.dzcard.length; t++) {
						for (let i = 0; i < that.customs.length; i++) {
							if (that.dzcard[t].id == that.customs[i]) {
								dzmoney = (Number(dzmoney) + Number(that.dzcard[t].balance)).toFixed(2);
							}
						}
					}
					if (dzmoney < zmoney) {
						that.$alert('定制卡余额不足,请选择其他支付方式！');
						return;
					}
				}
			}
		},
		//选择定制卡
		printdzCard(e) {
			var i = e.currentTarget.dataset.index;
			if (that.dzcard[i]['is_select']) {
				that.dzcard[i]['is_select'] = false;
				for (let t = 0; t < that.customs.length; t++) {
					if (that.customs[t] == that.dzcard[i].id) {
						that.customs.splice(t, 1);
					}
				}
			} else {
				that.dzcard[i]['is_select'] = true;
				that.customs.push(that.dzcard[i].id);
			}
			that.$forceUpdate();
			console.log('选择的定制卡：' + that.customs);
		},
		shouh(e) {
			uni.navigateTo({
				url: '/pages/order/shouhou/shouhou?id=' + e.currentTarget.dataset.order_detai_id
			});
		},
		// 支付
		pay() {
			var dzmoney = 0;
			var zmoney = that.new_total_price;
			// 选择定制卡支付
			if (that.paylist[that.payaction].type == 'BALANCE_PAY') {
				that.tanchuang
					.showModel({
						title: '提示',
						content: '你的账户余额为：' + that.userinfo.money + '元,是否确认支付',
						cancelText: '取消',
						confirmText: '确定'
					})
					.then(res => {
						console.log('操作结果：', res);
						if (res == 'confirm') {
							if (Number(that.userinfo.money) < Number(that.zmoney)) {
								that.$alert('您的余额不足!');
								return;
							} else {
								that.$pay.pay(that.paylist[that.payaction].type, that.gid).then(res => {
									if (res.code == 1) {
										console.log('支付失败！');
										that.$alert('支付失败！');
									} else {
										console.log('支付成功！');
										uni.setStorageSync('sx', true);
										setTimeout(function() {
											that.$alertok('支付成功！');
										}, 1000);
										setTimeout(function() {
											uni.navigateBack({
												delta: 1
											});
										}, 1000);
									}
								});
							}
						}
					});
			} else {
				that.$pay.pay(that.paylist[that.payaction].type, that.gid).then(res => {
					if (res.code == 1) {
						console.log('支付失败！');
						that.$alert('支付失败！');
					} else {
						console.log('支付成功！');
						setTimeout(function() {
							that.$alertok('支付成功！');
						}, 1000);
						setTimeout(function() {
							uni.setStorageSync('sx', true);
							uni.navigateBack({
								delta: 1
							});
						}, 1000);
					}
				});
			}
		}
	},
	onLoad(option) {
		console.log(option);
		that = this;
		// that.gid = option.id;
		// that.getinfo();
		// that.userinfo = uni.getStorageSync('userinfo');
		that.goodslist = orderlist.orderinfo.data.goods_list;
		that.data = orderlist.orderinfo.data;
	}
};
